.floor-wrap{
  margin-bottom: 40px;
}
.floor-item{
  margin-top: 40px;
}
.floor-item .floor-title{
  position: relative;
  height: 21px;
  line-height: 21px;
  font-size: 20px;
  color: #222222;
}
.floor-item .floor-title .floor-name{
  position: relative;
  z-index: 1
}
.floor-item .floor-title .text-border{
  position: absolute;
  bottom:6px;
  left:0;
  right: 0;
  height: 6px;
  background-color: #fdd000;
}
.floor-goods{
  display: flex;
  border-top: 2px solid #332200;
  margin-top: 8px;
}
.floor-goods .special-img{
  display: block;
  width: 244px;
  height: 442px;
  background-color:red;
}
.goods-wrap{
  flex:1;
  font-size: 0;
}
.goods-wrap .goods-item{
  display: inline-block;
  width:25%;
  height: 221px;
  border-right: 1px solid #e0e0e0;
  border-bottom: 1px solid #e0e0e0;
}
.goods-wrap .goods-item .goods-img{
  display:block;
  width:240px;
  height: 222px;
}